@import '../../lib/sass/px2rem';
@import '../../component/baseBtn/baseBtn';
@import '../../component/baseRadioItem/baseRadioItem';
@import '../../component/baseInputItem/baseInputItem';
@import '../../component/the-promise/the-promise';
@import '../../component/select/select';
.main { background: #f6f6f6;
    .degree_education_title { font-size: q(32); line-height: q(36); color: #333; padding: q(28) q(36);}
    .degree_education_radio { background: #fff; border-top: 1px solid #e6e6e6; padding: 0 q(36);}
    .promise { padding: q(18) q(36);
        .pro_detail { font-size: q(24);}
    }
    .degree_education_form { background-color: #fff; border: 1px solid #e6e6e6; margin: 0 q(20); padding-bottom: q(40);
        .form_item { border-bottom: 1px solid #f2f2f2; margin: 0 q(6); padding: 0 q(14);
            .input_item {
                .input_wraper { flex: 1; font-size: q(28);
                    .input_wraper_item { font-size: q(28);}
                }
            }
        }
    }
    .btn_wraper { display: flex; align-items: center; justify-content: space-between; padding: q(115) q(20) q(62); background: #fff; margin-top: q(38); border-top: 1px solid #e6e6e6;
        .btn { width: q(345);
            + .btn { margin-left: q(18);}
        }
    }
}

